.App {
  text-align: center;
}

.App-logo {
  height: 60vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.si{
  border-radius: 15px;
}

.blank{
  color: #61dafb;
  background-color: rgba(0, 0, 0, 0.393);
  padding: 10px;
  border-radius: 10px;
  cursor: pointer;
  width: 50px;
  text-align: center;
}

.ti{
  margin: 20px;
  padding: 10px;
  border-radius: 10px;
  cursor: pointer;
  background-color:rgba(214, 152, 45, 0.819);
}

.ci{
  color: black;
  background-color: orange;
  cursor: pointer;
  border-radius: 10px;
}